import { useMutation, useQuery } from '@tanstack/react-query'
import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import { getClassList } from '../../../../../api/Class/ClassApi'
import { addClassToSabum } from '../../../../../api/Sabum/sabum'
import { useDebug } from '../../../../../hooks/basic/useDebug'
import dojangIdAtom from '../../../../../recoil/atom/dojangIdAtom'
import EditBtnSM from '../../../../../components/UI/Atoms/Button/EditBtnSM'
import Checkbox from '../../../../../components/UI/Atoms/Checkbox/Checkbox'

const AddClass = ({ info }) => {
  const dojangId = useRecoilValue(dojangIdAtom)
  const [ classList, setClassList ] = useState([])
  const sabumId = info?.id

  // fn
  const getClassIdList = (v) => {
    let _list = []
    v.forEach(item => _list.push(item?.id))
    return _list
  }

  const getAddList = () => {
    let _list = classList.filter(v => (v.isCheck === true) && (v.isAlready === false))
    let temp = _list.map(v => v.id)
    return temp
  }

  // query
  const { data } = useQuery(['getClassList', dojangId], () => getClassList(dojangId), {
    enabled: !!dojangId,
    onSuccess: (res) => {
      const idList = getClassIdList(info?.Classes)
      
      let temp = []
      res.rows.forEach( item => {
        let bool = idList.includes(item.id)
        temp.push({ ...item, isAlready:bool, isCheck:bool })
      })
      setClassList(temp)
    }
  })

  const { mutate: addMutate, isLoading } = useMutation((body) => addClassToSabum(dojangId, sabumId, body))

  // handler
  const handleCheck = (e) => {
    const { value, checked } = e.target
    const temp = [...classList]
    temp[value] = {...temp[value], isCheck:checked }
    setClassList(temp)
  }

  const handleAdd = (e) => {
    let _list = getAddList()
    let body = { class_arr: _list}
    addMutate(body, {
      onSuccess: () => window.location.reload()
    })
  }

  // useDebug(info)
  return (
    <>
    <div className='flex flex-col justify-center mt-[20px] mb-[20px]'>
      { classList &&
        React.Children.toArray(
          classList?.map( (item, idx) => 
            <div className='flex justify-between my-1 px-4 py-2 items-center bg-[#F5F7F8] h-[50px] rounded-[5px]'>
              {item?.title}
              <Checkbox 
              type='checkbox' 
              value={idx} id={item?.id} 
              onChange={handleCheck} 
              checked={item?.isCheck} 
              disabled={item?.isAlready}
               />
            </div>
          )
          )}
    </div>
    <div className="flex justify-center">
      <EditBtnSM onClick={handleAdd} disabled={isLoading}>추가</EditBtnSM>
    </div>
    </>
  )
}

export default AddClass