import { useMutation } from '@tanstack/react-query';
import React, { useState } from 'react';
import { useRecoilValue } from 'recoil';
import { getSabumSpecInfo, outSabumToClass } from '../../../../../api/Sabum/sabum';
import AlertText from '../../../../../components/common/Alert/AlertText';
import ClassPop from '../../../../../components/common/Class/ClassPop';
import PopWrap from '../../../../../components/common/Wrap/PopWrap';
import Box from '../../../../../components/layout/Box';
import ModalContainer from '../../../../../components/layout/ModalContainer';
import ModalContainerAlert from '../../../../../components/layout/ModalContainerAlert';
import ModalContainerSM from '../../../../../components/layout/ModalContainerSM';
import ModalContainerSub from '../../../../../components/layout/ModalContainerSub';
import EditBtnSM from '../../../../../components/UI/Atoms/Button/EditBtnSM';
import Name from '../../../../../components/UI/Atoms/Ex/Name';
import InfoData from '../../../../../components/UI/Atoms/Info/InfoData';
import InfoLabelSM from '../../../../../components/UI/Atoms/Info/InfoLabelSM';
import ProfileImage from '../../../../../components/UI/Atoms/Profile/ProfileImage';
import { useDebug } from '../../../../../hooks/basic/useDebug';
import { useModal } from '../../../../../hooks/basic/useModal';
import dojangIdAtom from '../../../../../recoil/atom/dojangIdAtom';
import { sabumInfoAtom } from '../recoil/sabumInfoAtom';
import AddClass from './AddClass';
import SabumSpecInfo from './SabumSpecInfo';

const SabumInfo = () => {
  // recoil
  const dojangId = useRecoilValue(dojangIdAtom);
  const sabumInfo = useRecoilValue(sabumInfoAtom);
  const { id } = sabumInfo;

  // state
  const [specInfo, setSpecInfo] = useState({});

  // custom hook
  const [open, openFn, closeFn] = useModal();
  const [openAdd, openAddFn, closeAddFn] = useModal();
  const [openOut, openOutFn, closeOutFn] = useModal();

  // query
  const specInfoMutation = useMutation(() => getSabumSpecInfo(dojangId, id));
  const { isLoading, mutate: outMutate } = useMutation(() => outSabumToClass(dojangId, id));

  // handler
  const handleClick = (e) => {
    specInfoMutation.mutate(id, {
      onSuccess: (res) => {
        setSpecInfo(res);
        openFn();
      },
    });
  };

  const handleAdd = (e) => {
    openAddFn();
  };

  const handleOut = (e) => {
    outMutate(id, {
      onSuccess: () => window.location.reload(),
    });
  };

  // useDebug(sabumInfo)

  return (
    <>
      <Box title="기본 정보" btn={<EditBtnSM onClick={handleClick}>상세정보</EditBtnSM>}>
        <div className="flex">
          <div className="basis-1/5">
            <ProfileImage size="140px" urlKey={sabumInfo?.photo_url} />
          </div>
          <div className="flex flex-col basis-4/5 justify-center">
            <Name>{sabumInfo?.last_name + sabumInfo?.first_name}</Name>
            <div className="flex items-center">
              <InfoLabelSM className="text-[15px]">생년월일</InfoLabelSM>
              <InfoData>{sabumInfo?.dob}</InfoData>
              <InfoLabelSM>입사일</InfoLabelSM>
              <InfoData>{sabumInfo?.dojang_reg_date}</InfoData>
            </div>
            <div className="flex items-center">
              <InfoLabelSM>전화번호</InfoLabelSM>
              <InfoData>{sabumInfo?.phone_number}</InfoData>
              <InfoLabelSM>단/급</InfoLabelSM>
              <InfoData>{sabumInfo?.level}</InfoData>
            </div>
            <div className="flex items-start">
              <InfoLabelSM >계정 정보</InfoLabelSM>
              <InfoData>{sabumInfo?.UserAccount?.username}</InfoData>
              <InfoLabelSM > </InfoLabelSM>
              <InfoData> </InfoData>
            </div>
          </div>
        </div>
      </Box>

      <ModalContainer open={open} onClose={() => closeFn()} title="사범 상세 정보">
        <SabumSpecInfo info={specInfo} />
      </ModalContainer>

      <Box title="담당반 정보" btn={<EditBtnSM onClick={handleAdd}>+ 반 추가</EditBtnSM>}>
        <PopWrap>
          {React.Children.toArray(
            sabumInfo?.Classes?.map((item) => (
              <ClassPop class_id={item?.id} class_name={item?.title} sabum_id={id} del />
            )),
          )}
        </PopWrap>
      </Box>

      <ModalContainerSM open={openAdd} onClose={closeAddFn} title="담당반을 선택합니다.">
        <AddClass info={sabumInfo} />
      </ModalContainerSM>

      <div className="flex justify-end">
        <EditBtnSM onClick={openOutFn} disabled={isLoading}>
          퇴사
        </EditBtnSM>
      </div>

      <ModalContainerAlert
        open={openOut}
        onClose={closeOutFn}
        onConfirm={handleOut}
        confirmText="퇴사"
      >
        <AlertText title>
          {sabumInfo?.last_name}
          {sabumInfo?.first_name} 사범님을 퇴사 처리합니다.
        </AlertText>
        <AlertText>
          퇴사 처리가 되면 사범의 정보가 삭제되며, <br />
          복구할 수 없습니다.
        </AlertText>
      </ModalContainerAlert>
    </>
  );
};

export default SabumInfo;
