import React from 'react'
import { styled } from '@mui/system';
import { FormControl, OutlinedInput } from '@mui/material';

const Label = ({ children, ...props }) => {
  return(
    <div className={`w-[88px] text-[#00196A] text-[14px]`} {...props}>
      { children }
    </div>
  )
}

const StyledFormControl = styled(FormControl)(({ theme }) => ({
  color: '#000000',
  '& .MuiOutlinedInput-root': {
    maxWidth: '186px',
    padding: '0 10px 0 0',
    fontSize: '14px',
    height: '30px',
    background: '#ffffff',
    color: '#000000',
    borderRadius: '0',
    letterSpacing: 'normal',
  },
  '& .MuiOutlinedInput-notchedOutline': {
    border: '1px solid #AAAAAA',
    padding: '5px 10px',
  },
  '& .Mui-disabled': {
    'WebkitTextFillColor': '#000000!important',
    '.MuiOutlinedInput-notchedOutline': {
    border: 'none',
    borderBottom: '1px solid #AAAAAA',
    },
  },
  '& .Mui-focused .MuiOutlinedInput-notchedOutline': {
    border: '1px solid #000000',
  },
}))

const Input = ({...props}) => {
  return(
    <StyledFormControl>
      <OutlinedInput {...props} />
    </StyledFormControl>
  )
}

const getColSize = (col) => {
  switch (col) {
    case 1: return 'flex-1'
    case 2: return 'flex-2'
    case 3: return 'flex-3'
    default: return ''
  }
}
const Col = ({ col, children, className, ...props }) => {
  return(
    <div className={`flex ${getColSize(col)} items-center gap-[10px] ${className || ''}`} {...props}>
      { children }
    </div>
  )
}

const Row = ({ children, className, ...props }) => {
  return(
    <div className={`flex items-center gap-[10px] mt-[10px] justify-between ${className || ''}`} {...props}>
      { children }
    </div>
  )
}

export { Label, Input, Col, Row }