import React from 'react';
import { useQuery } from '@tanstack/react-query';
import ModalContainer from '../../layout/ModalContainer';
import Box from '../../layout/Box';
import { getClassInfo } from '../../../api/Class/ClassApi';
import InfoLabelSM from '../../UI/Atoms/Info/InfoLabelSM';
import InfoData from '../../UI/Atoms/Info/InfoData';
import ClassSchedule from '../Table/ClassSchedule';
import { useRecoilValue } from 'recoil';
import dojangIdAtom from '../../../recoil/atom/dojangIdAtom';
import { useDebug } from '../../../hooks/basic/useDebug';
import { getFullname } from '../../../utils/render/getFullname';


const ClassModal = ({ open, onClose, classId }) => {
  const dojangId = useRecoilValue(dojangIdAtom)
  const classInfo = useQuery(['getClassInfo', classId], () => getClassInfo(classId, dojangId), {
    enabled: !!classId,
  })

  // useDebug(classInfo.data)

  return (
    <ModalContainer open={open} onClose={onClose} title="반 상세 정보">
      <Box title="시간표">
        <div className="flex mt-[10px] w-[260px] items-center">
          <InfoLabelSM>반 이름</InfoLabelSM>
          <InfoData className="text-[15px] border-solid border-b border-x-0 border-t-0 border-[#aaaaaa] pl-[10px] leading-7">
            {classInfo?.data?.title}
          </InfoData>
        </div>
        <div className="mt-[10px]">
          {classInfo?.data && <ClassSchedule classId={classId} data={classInfo?.data} />}
        </div>
      </Box>
      <Box title={`담당 사범님  (${classInfo?.data?.Sabums?.length || '0'}명)`}>
        <div className="h-[60px] mt-[10px]">
          {classInfo?.data &&
            React.Children.toArray(
              classInfo?.data?.Sabums?.map((item) => (
                <div className="w-[100px] h-[30px] float-left mr-[10px]">
                  {getFullname(item?.last_name, item?.first_name)} ,
                </div>
              )),
            )}
        </div>
      </Box>
      <Box title={`소속 수련생  (${classInfo?.data?.StudentInfos?.length || '0'}명)`}>
        <div className="h-[180px] mt-[10px]">
          {classInfo?.data &&
            React.Children.toArray(
              classInfo?.data?.StudentInfos?.map((item) => (
                <div className="w-[100px] h-[30px] float-left mr-[10px]">
                  {getFullname(item?.last_name, item?.first_name)} ,
                </div>
              )),
            )}
        </div>
      </Box>
    </ModalContainer>
  );
};

export default ClassModal;
