import React, { useState } from 'react'
import ClassModal from './ClassModal'
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import { useModal } from '../../../hooks/basic/useModal';
import { styled } from '@mui/system';
import { Box } from '@mui/material'
import EditBtnSM from '../../UI/Atoms/Button/EditBtnSM';
import { useMutation } from '@tanstack/react-query';
import { delTeacherToClass } from '../../../api/Class/ClassApi';

const StyledBox = styled(Box)(({ theme }) => ({
  width: '260px',
  display: 'flex',
  gap: '0.5rem',
  border: '1px solid #dddddd',
  borderRadius: '5px',
  height: '50px',
  backgroundColor: theme.palette.background.secondary,
  justifyContent: 'space-between',
  alignItems: 'center',
  padding: '0 20px',
  cursor: 'pointer',
  
  '&:hover': {
    backgroundColor: theme.palette.background.hover,
  },
  '&:active': {
    backgroundColor: theme.palette.background.active,
  },
}));



const ClassPop = ({ class_id, class_name, sabum_id, del }) => {
  const [open, openFn, closeFn] = useModal()

  // query
  const { mutate: delClassToSabum, isLoading} = useMutation(() => delTeacherToClass(class_id, ['sabum', sabum_id]))


  // handler
  const handleDel = () => {
    delClassToSabum(sabum_id, {
      onSuccess: () => window.location.reload()
    })
  }
  
  if (del) {
    return (
      <>
      <StyledBox>
        <div className='text-[16px]'>
          {class_name}
        </div>
        <div className="flex jsutify-center items-center">
          <EditBtnSM onClick={handleDel} disabled={isLoading}>삭제</EditBtnSM>
        </div>
      </StyledBox>
      </>
    )
  }

  return (
    <>
    <StyledBox 
      onClick={()=>openFn()}
    >
      <div className='text-[16px] text-[#000000]'>
        {class_name}
      </div>
      <div className="flex jsutify-center items-center">
        <ArrowForwardIosIcon fontSize="10px" className="text-[#545454]" />
      </div>
    </StyledBox>
    
    <ClassModal
      open={open}
      onClose={()=>closeFn()}
      classId={class_id}
    />
      
  </>

  )
}

export default ClassPop