import { useQuery } from '@tanstack/react-query'
import React from 'react'
import { useRecoilValue } from 'recoil'
import { getSabumList } from '../../../../api/Sabum/sabum'
import ClassSelectMD from '../../../../components/common/Class/ClassSelectMD'
import PageContainer from '../../../../components/layout/PageContainer'
import { useDebug } from '../../../../hooks/basic/useDebug'
import { useModal } from '../../../../hooks/basic/useModal'
import { useSelect } from '../../../../hooks/basic/useSelect'
import dojangIdAtom from '../../../../recoil/atom/dojangIdAtom'
import SabumList from './SabumList'


const ManageSabumPage = () => {
  const dojangId = useRecoilValue(dojangIdAtom)

  // custom hook
  const [classes, handleChangeClasses] = useSelect('0')
  const [open, openFn, closeFn] = useModal()

  // query
  const sabums = useQuery(['getSabumList', classes, dojangId], () => getSabumList(dojangId, classes), {
    enabled: !!classes && !!dojangId,
  })


  return (
    <>
    <PageContainer width="md">
      <div className='flex justify-between items-center mb-3 w-full'>
        <div className="flex gap-[10px]">
          <ClassSelectMD value={classes} onChange={handleChangeClasses} />
        </div>
      </div>
      { sabums?.data && <SabumList data={sabums.data.rows}/> }
    </PageContainer>
    </>
  )
}

export default ManageSabumPage