import React from 'react'
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 500,
  height: 470,
  bgcolor: '#ffffff',
  borderRadius: '10px',
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
  overflow:'auto'
};

const ModalContainerSM = ({ open, onClose, title, children }) => {

  return (
    <>
    <Modal
      open={open}
      onClose={onClose}
    >
      <Box sx={style}>
        <div className='flex my-3'>
          <div className="text-[22px] font-bold">
            {title}
          </div>
        </div>

        {children}
      </Box>
    </Modal>
    </>
  )
}

export default ModalContainerSM