import React from 'react'
import RankImage from '../../../../../components/common/Rank/RankImage'
import Box from '../../../../../components/layout/Box'
import ProfileImage from '../../../../../components/UI/Atoms/Profile/ProfileImage'
import { useDebug } from '../../../../../hooks/basic/useDebug'
import { phoneHyphenRegex } from '../../../../../utils/lib/Regex'
import { getFullname } from '../../../../../utils/render/getFullname'
import { toGender } from '../../../../../utils/render/toGender'
import { Col, Input, Label, Row } from '../../Student/component/StudentInfo/StudentForm/StudentForm'

const SabumSpecInfo = ({ info }) => {

  console.log(info)

  // useDebug(info)

  return (
    <>
      <Box title='기본 정보'>
        <div className="flex items-center gap-[20px]">
          <div className='flex flex-col items-center relative'>
            <ProfileImage
              size='140px'
              urlKey={info.photo_url}
            />
          </div>
          <div className='flex flex-col'>
            <Row>
              <Col col={1} className='mr-[45px]'>
                <Label>이름</Label>
                <div className="flex flex-1">
                  <Input
                    defaultValue={getFullname(info?.last_name,info?.first_name)}
                    disabled
                  />
                </div>
              </Col>
              <Col col={1} className="justify-end">
                <Label>성별</Label>
                <Input
                  defaultValue={toGender(info?.sex)}
                  disabled
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>생년월일</Label>
                <Input
                  defaultValue={info?.dob}
                  disabled
                />
              </Col>
              <Col>
                <Label>입사일</Label>
                <Input
                  defaultValue={info?.dojang_reg_date}
                  disabled
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>전화번호</Label>
                <Input
                  defaultValue={phoneHyphenRegex(info?.phone_number)}
                  disabled
                />
              </Col>
              <Col>
                <Label>메일</Label>
                <Input
                  defaultValue={info?.email}
                  disabled
                />
              </Col>
            </Row>
            <Row>
              <Col className='w-[100%]'>
                <Label>주소</Label>
                <div className="flex">
                  <Input
                    defaultValue={(info?.address_name || '')}
                    disabled
                    className='min-w-[340px]'
                  />
                  <Input
                    defaultValue={(info?.address_detail || '')}
                    disabled
                  />
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </Box>
      <Box title='최종 학력'>
        <div className='flex flex-col'>
          <Row>
            <Col col={1}>
              <Label>재학기간</Label>
              <Input
                defaultValue={((info?.admission_year ? (`${info?.admission_year}` + '.'+ `${info?.admission_month}` + '~') : '' ) + (info?.graduated_year && (`${info?.graduated_year}` + '.' + `${info?.graduated_month}`)))}
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>대학명</Label>
              <Input
                defaultValue={info?.school_name}
                disabled
              />
            </Col>
          </Row>
          <Row>
            <Col col={1}>
              <Label>전공</Label>
              <Input
                defaultValue={info?.major}
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>학점</Label>
              <Input
                defaultValue={(info?.grades && info?.grades + '/ 4.5')}
                disabled
              />
            </Col>
          </Row>
        </div>
      </Box>
      <Box title='자격증'>
        <div className='flex flex-col'>
          <Row>
            <Col col={1}>
              <Label>자격증명</Label>
              <Input
                defaultValue='태권도 단증'
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>단/급</Label>
              <Input
                defaultValue={info?.level}
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>일련번호</Label>
              <Input
                defaultValue={info?.tkd_level_number}
                disabled
              />
            </Col>
          </Row>
          <Row>
            <Col col={1}>
              <Label>자격증명</Label>
              <Input
                defaultValue='사범 자격증'
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>단/급</Label>
              <Input
                defaultValue={info?.instructor_license_grade}
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>일련번호</Label>
              <Input
                defaultValue={info?.instructor_license_number}
                disabled
              />
            </Col>
          </Row>
          <Row>
            <Col col={1}>
              <Label>자격증명</Label>
              <Input
                defaultValue='생활체육지도자'
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>단/급</Label>
              <Input
                defaultValue={info?.life_sports_instructor_grade}
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>일련번호</Label>
              <Input
                defaultValue={info?.life_sports_instructor_number}
                disabled
              />
            </Col>
          </Row>

          { React.Children.toArray(
              info?.sabum_license?.map( item => 
                <Row>
                  <Col col={1}>
                    <Label>자격증명</Label>
                    <Input
                      defaultValue={item?.license_name}
                      disabled
                    />
                  </Col>
                  <Col col={1}>
                    <Label>단/급</Label>
                    <Input
                      defaultValue={item?.license_grade}
                      disabled
                    />
                  </Col>
                  <Col col={1}>
                    <Label>일련번호</Label>
                    <Input
                      defaultValue={item?.license_number}
                      disabled
                    />
                  </Col>
                </Row>
              )
          )}
        </div>
      </Box>
      <Box title='태권 정보'>
        <div className='flex flex-col'>
          <Row>
            <Col col={1}>
              <Label>태권도 경력</Label>
              <Input
                defaultValue={info?.career}
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>단/급</Label>
              <Input
                defaultValue={info?.level}
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>띠</Label>
              <RankImage
                src={info?.level}
                disabled
              />
            </Col>
          </Row>
        </div>
      </Box>
      <Box title='사범 경력'>
        <div className='flex flex-col'>
          { React.Children.toArray(
              info?.Sabum_career?.map( item => 
                <Row>
                  <Col col={1}>
                    <Label>도장명</Label>
                    <Input
                      defaultValue={item?.dojang_name}
                      disabled
                    />
                  </Col>
                  <Col col={1}>
                    <Label>재직기간</Label>
                    <Input
                      defaultValue={(item?.employment_start_year && (`${item?.employment_start_year}` + '.' + `${item?.employment_start_month}` + '~')) + (item?.employment_end_year && (`${item?.employment_end_year}` + '.' + `${item?.employment_end_month}`))}
                      disabled
                    />
                  </Col>
                </Row>
              )
          )}
        </div>
      </Box>
      <Box title='운전 면허'>
        <div className='flex flex-col'>
          <Row>
            <Col col={1}>
              <Label>면허</Label>
              <Input
                defaultValue={info?.driver_license}
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>취득일</Label>
              <Input
                defaultValue={info?.driver_license_obtaining_year && (`${info?.driver_license_obtaining_year}` + '.' + `${info?.driver_license_obtaining_month }`)}
                disabled
              />
            </Col>
          </Row>
        </div>
      </Box>
    
    </>
  )
}

export default SabumSpecInfo