import { useMutation } from '@tanstack/react-query'
import React from 'react'
import { useRecoilValue, useSetRecoilState } from 'recoil'
import { getSabumInfo } from '../../../../api/Sabum/sabum'
import { Grid } from '../../../../components/common/Grid/Grid'
import ModalContainer from '../../../../components/layout/ModalContainer'
import Description from '../../../../components/UI/Atoms/Ex/Description'
import Em from '../../../../components/UI/Atoms/Ex/Em'
import ArrowIcon from '../../../../components/UI/Atoms/Icon/ArrowIcon'
import ProfileImage from '../../../../components/UI/Atoms/Profile/ProfileImage'
import { useDebug } from '../../../../hooks/basic/useDebug'
import { useModal } from '../../../../hooks/basic/useModal'
import dojangIdAtom from '../../../../recoil/atom/dojangIdAtom'
import { phoneHyphenRegex } from '../../../../utils/lib/Regex'
import { sabumInfoAtom } from './recoil/sabumInfoAtom'
import SabumInfo from './SabumInfo/SabumInfo'

const SabumList = ({ data }) => {
  // recoil
  const dojangId = useRecoilValue(dojangIdAtom)
  const setSabumInfo = useSetRecoilState(sabumInfoAtom)

  // custom hook
  const [ open, openFn, closeFn ] = useModal()

  // query
  const { mutate: sabumMt } = useMutation((sabumId) => getSabumInfo(dojangId, sabumId))
  const getInfo = (sabumId) => {
    sabumMt(sabumId, {
      onSuccess: (res) => {
        setSabumInfo(res)
        openFn()
      }
    })
  }
  
  // handler
  const handleClickItem = (e) => {
    const clickId = e.currentTarget.id
    getInfo(clickId)
  }

  // useDebug(data)

  return (
    <>
    <Grid>
      <Grid.Head>
        <Description>현재 선택된 반의 사범님은 <Em>{data?.length || '0'}명</Em>입니다.</Description>
      </Grid.Head>
      <Grid.RowWrap>
        { React.Children.toArray(
          data?.map(item => 
            <Grid.Row
              id={item?.id}
              onClick={handleClickItem}
            >
              <Grid.Col fixed='85px'><ProfileImage urlKey={item?.photo_url} /></Grid.Col>
              <Grid.Col fixed='200px' className='pl-0'>{item?.last_name}{item?.first_name}</Grid.Col>
              <Grid.Col fixed='225px'>{phoneHyphenRegex(item?.phone_number)}</Grid.Col>
              <Grid.Col fixed='250px'>입사일 : {item?.dojang_reg_date}</Grid.Col>
              <Grid.Col fixed='225px'>담당반 : {item?.Classes?.length}개반</Grid.Col>
              <Grid.Col><ArrowIcon /></Grid.Col>
            </Grid.Row>
          )
        )}
      </Grid.RowWrap>
    </Grid>

    <ModalContainer
      open={open}
      onClose={()=>closeFn()}
      title='사범 정보'
    >
      <SabumInfo />
    </ModalContainer>
    </>
  )
}

export default SabumList