import React, { useMemo } from 'react'
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import BackBtn from '../UI/Atoms/Button/BackBtn';


const ModalContainerSub = ({ width, height, open, onClose, title, children }) => {
  const style = useMemo(() => {
    return {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: width || 500,
      height: height || 600,
      bgcolor: '#F2F2F2',
      borderRadius: '10px',
      boxShadow: 24,
      pt: '30px',
      px: '30px',
      pb: '30px',
      overflow:'auto'
    }
  }, [])


  return (
    <>
    <Modal
      open={open}
      onClose={onClose}
    >
      <Box sx={style}>
        <div className='flex items-center'>
          <div>
            {title}
          </div>
        </div>

        {children}
      </Box>
    </Modal>
    </>
  )
}

export default ModalContainerSub